* {
    box-sizing: border-box;
    font-family: 'DM Sans', sans-serif;
    color: white;
}

.navbar {
    > h1 {
        margin: 0;
    }

    .characters-box {

        > img {
           width: 50px;
           height: 50px;
        }
   }
}

.start-background {
    height: 100vh;
    background-image: url('../../public/dino-blur.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.startBox {
    display: flex;
    flex-direction: column;
    position: fixed;
    background-color: #64B6A0;
    box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 1);
    width: 60%;
    max-width: 636px;
    top: 50%;
    right: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: black;
    padding: 1em;

    > h1 {
        font-size: clamp(2.0625rem, 1.7941rem + 1.227vw, 3.3125rem);
        text-align: center;
    }

    .start-instructions {
        font-size: clamp(1.125rem, 0.9908rem + 0.6135vw, 1.75rem);

        > p {
            padding: 0.5em;
        }
    }

    > h1, p {
        margin: 0;
    }

    > button {
        background: #2C816D;
        max-width: 320px;
        text-decoration: none;
        border: none;
        line-height: normal;
        padding: 0.6em;
        font-size: clamp(1.4375rem, 1.2765rem + 0.7362vw, 2.1875rem);
        border-right: 3px solid #000000;
        border-bottom: 3px solid #000000;
        margin: 0 auto;

        &:hover {
            cursor: pointer;
            transform: scale(1.05);
        }
    }
}

.error-msg {
    color:red;
}

.scoreboard {
    position: fixed;
    background-color: #64B6A0;
    width: 50vmin;
    top: 50%;
    right: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: black;
    display: flex;
    flex-direction: column;

    > h3 {
        font-size: clamp(1.125rem, 0.75rem + 1.6667vw, 2.8125rem);
        margin: 0;
        align-self: center;
    }

    .topUserBox {
        display: flex;
        justify-content: space-between;
        margin: 0.5em;
    }

    > ul {
        font-size: clamp(1.125rem, 0.9167rem + 0.9259vw, 2.0625rem);
        margin: 0;
        list-style-type: none;
        padding: 0;
    }

    & p {
        margin: 0;
    }
}

